.advantages {
    padding: 50px 0 130px;
    // background-image: url('../img/advantages_bg.jpg');
    // background-repeat: no-repeat;
    // background-position: 0 0;
    background-size: cover;
    max-width: 1920px;
    margin: 0 auto;

    position: relative;

    display: flex;
    align-items: center;
    overflow: hidden;

    &::before  {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 50px;
        background-repeat: repeat;
        background-image: url('../img/frame2.png');
        background-size: contain;
        z-index: 1;
    }

    &__item-wrapper {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-end;
    }

    &__item {
        width: 600px;
        position: relative;
        padding: 0 10px 30px;
        border-radius: 30px;
        border: 2px solid transparent;
        transition: ease .5s all;
        position: relative;
        z-index: 2;

        &--1 {
            margin-right: 607px;
        }

        &--2 {
            margin-right: 840px;
        }

        &--3 {
            margin-right: 523px;
        }

        &--4 {
            margin-right: 690px;
        }

        span {
            position: absolute;
            top: 30px;
            left: 30px;
            @include secondary-title;
            font-size: 16px;
            line-height: 24px;
            color: $secondary-color;
            position: relative;
            background: linear-gradient(90deg, #ffdae9 0%, #fef5e4 9%, #fef5e4 93%, #ffdae9 100%);
            border-radius: 50%;
            width: 50px;
            height: 50px;
            display: flex;
            justify-content: center;
            align-items: center;

            &::before {
                content: "";
                position: absolute;
                top: 10px;
                left: 10px;
                width: 30px;
                height: 30px;
                border-radius: 50%;
                border: 1px solid $secondary-color;
            }

            &::after {
                content: "";
                position: absolute;
                top: 0px;
                left: 0px;
                width: 50px;
                height: 50px;
                border-radius: 50%;
                border: 1px solid $secondary-color;
            }
        }

        &:hover,
        &:focus {
            background-color: rgba(255, 255, 255, 0.8);
            border: 2px solid #ffdae9;
            transition: ease .5s all;
        }
    }

    &__title {
        @include secondary-title;
        font-style: italic;
        font-size: 16px;
        line-height: 24px;
        font-weight: 600;
        padding-left: 100px;
        padding-bottom: 10px;
        margin-bottom: 10px;
        text-transform: uppercase;
    }

    &__description {
        padding-left: 100px;
    }

    &__logo-wrapper {
        position: absolute;
        top: 50px;
        right: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 500px;
        height: 500px;
        border: 1px solid $secondary-color;
        border-radius: 50%;
        position: absolute;
        top: -50px;
        right: 200px;

        img {
            border-radius: 50%;
        }

        &::before {
            content: "";
            position: absolute;
            width: 940px;
            height: 940px;
            border: 1px solid $secondary-color;
            border-radius: 50%;
            top: -220px;
            left: -220px;
        }

        &::after {
            content: "";
            position: absolute;
            width: 1400px;
            height: 1400px;
            border: 1px solid $secondary-color;
            border-radius: 50%;
            top: -450px;
            left: -450px;
        }
    }

    &__decor-circle {
        position: absolute;
        width: 1875px;
        height: 1875px;
        border: 1px solid $secondary-color;
        border-radius: 50%;
        top: -687.5px;
        left: -687.5px;
    }
}

@media (max-width: 1500px) {
    .advantages {
        padding: 50px 0;

        &__logo-wrapper {
            width: 450px;
            height: 450px;
        }

        &__logo-wrapper::before {
            width: 840px;
            height: 840px;
            top: -200px;
            left: -200px;
        }

        &__logo-wrapper::after {
            width: 1200px;
            height: 1200px;
            top: -390px;
            left: -390px;
        }

        &__decor-circle {
            width: 1600px;
            height: 1600px;
            top: -600px;
            left: -601px;
        }

        &__item {
            padding-bottom: 10px;

            span {
                top: 10px;
                left: 10px;
            }

            &--1 {
                margin-right: 480px;
            }

            &--2 {
                margin-right: 685px;
            }

            &--3 {
                margin-right: 425px;
            }

            &--4 {
                margin-right: 30px;
            }
        }

        &__title {
            margin-top: -30px;
        }
    }
}

@media (max-width: 1300px) {
    .advantages {
        &__item {
            &--2 {
                margin-right: 470px;
            }

            &--4 {
                margin-right: 330px;
            }
        }
    }
}

@media (max-width: 1100px) {
    .advantages {
        padding: 50px 30px;
        &__item-wrapper {
            align-items: flex-start;
        }

        &__item {
            background-color: rgba(255, 255, 255, 0.8);
            border: 2px solid #ffdae9;
            transition: ease .5s all;
            margin-bottom: 30px;
            width: 100%;
            max-width: 600px;
        }
    }
}

@media (max-width: 500px) {
    .advantages {
        &__item {
            padding: 20px;
            span {
                display: none;
            }
        }

        &__title {
            margin: 0;
            padding-left: 0;
        }

        &__description {
            padding-left: 0;
        }
    }
}
