.footer {
    max-width: 1920px;
    width: 100%;
    margin:  0 auto;
    display: flex;

    background-repeat: repeat;
    background-position: 0 0;
    background-size: auto;
    background-image: url('../img/bg-1.jpg');
    position: relative;
    border-bottom: 5px solid rgba(255,218,233,1);

    &::before  {
        content: "";
        position: absolute;
        bottom: -25px;
        left: 0;
        width: 100%;
        height: 50px;
        background-repeat: repeat;
        background-image: url('../img/frame1.png');
    }

    &__copyright {
        width: 40%;
        background-color: rgba(255,255,255,0.7);
        padding: 50px 0 50px 100px;
    }

    &__col {
        display: flex;
        align-items: center;
    }

    &__social {
        display: flex;
        flex-direction: column;
    }

    &__social-wrapper {
        display: flex;
        justify-content: center;
    }

    &__nav {
        margin-right: 50px;
    }

    &__list-item {
        margin-bottom: 15px;
    }

    &__link {
        font-family: 'Lemonada', cursive;
        font-size: 16px;
        line-height: 22px;
        color: $primary-color;
        margin-bottom: 20px;
        position: relative;

        &::after {
            position: absolute;
            content: "";
            top: 100%;
            left: 0;
            width: 100%;
            height: 3px;
            background: $secondary-color;
            transform: scaleX(0);
            transform-origin: right;
            transition: transform 0.5s;
        }
      
        &:hover {
            color: $primary-color;
            text-decoration: none;
        }
      
        &:hover::after {
            transform: scaleX(1);
            transform-origin: left;
        }
    }

    &__phone {
        @include secondary-title;
        color: $secondary-color;
        transition: all ease .5s;
        // text-shadow: 1px 2px 1px black;
        padding-right: 30px;
        width: 300px;
        display: block;

        position: relative;

        &::before {
            content: "";
            position: absolute;
            top: 25%;
            right: 0;
            width: 25px;
            height: 25px;
            background-image: url('../img/svg/callMe.svg');
            background-position: 0 0;
            background-size: contain;
            background-repeat: no-repeat;
        }

        &:hover,
        &:focus {
            color: $primary-color;
            text-decoration: none;
            transition: all ease .5s;
        }

        &:active {
            opacity: 0.5;
        }
    }

    &__social {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__social-wrapper {
        height: 50px;
        width: 100%;
    }

    &__item {
        margin: 10px;
        transition: ease .5s all;

        &:hover {
            transform: scale(1.2);
            transition: ease .5s all;
        }
    }

    &__insta {
        width: 30px;
        height: 30px;
        background-repeat: no-repeat;
        background-position: 0 0;
        background-size: contain;
        background-image: url('../img/svg/instagram.svg');
    }

    &__whatapp {
        width: 30px;
        height: 30px;
        border-radius: 7px;
        background-repeat: no-repeat;
        background-position: 0 0;
        background-size: contain;
        background-image: url('../img/svg/whatsapp.svg');
    }

    address {
        font-weight: 600;
    }

    .map {
        width: 60%;
        height: 300px;
    }

    .map iframe {
        width: 100%;
        height: 300px;
        border: none;
    }
}

@media (max-width: 1500px) {
    .footer {
        &__copyright {
            padding-left: 30px;
            width: 50%;
        }

        .map {
            width: 50%;
        }
    }
}

@media (max-width: 1200px) {
    .footer {
        &__phone {
            font-size: 18px;
            width: 210px;
            margin: 0 auto;
        }

        address {
            font-size: 14px;
        }

        &__nav {
            margin-right: 20px;
        }
    }
}

@media (max-width: 992px) {
    .footer {
        flex-direction: column;

        &__col {
            flex-direction: column;
        }

        &__nav {
            display: flex;
            justify-content: space-around;
            align-items: center;
            flex-wrap: wrap;
        }

        &__list-item {
            margin: 0 10px;
            margin-bottom: 10px;
        }

        &__copyright {
            width: 100%;
        }

        .map {
            width: 100%;
        }
    }
}

@media (max-width: 768px) {
    .footer {
        &__copyright {
            padding: 30px;
        }
    }
}
